.popup {
  width: 100%;
  height: 100%;

  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s linear;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  color: black;
}

.popup__container {
  margin: 0;
  padding: 0;
  display: flex;
  position: relative;
}


.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__close-button {
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  background: none;
  background-position: center;
  background-image: url(../images/closeBlack.svg);
  background-repeat: no-repeat;
  background-size: 16px 16px;
  border: none;
  position: absolute;
  top: -40px;
  right: -40px;
  transition: opacity 0.2s linear;
  background-color: antiquewhite;
  border-radius: 50%;
}

.popup__close-button:hover {
  /*opacity: 0.6;*/
  cursor: pointer;
  background-color: #696355;
  background-image: url(../images/closeWhite.svg);
}

@media (max-width: 767px) {
  .popup__close-button {
    width: 22px;
    height: 22px;
    top: -25px;
    right: -25px;
    background-size: 11px 11px;
  }
}

@media (max-width: 470px) {
  .popup__close-button {
    width: 20px;
    height: 20px;
    top: -30px;
    right: -6px;
    background-size: 10px 10px;
  }
}

@media (max-width: 380px) {
  .popup__close-button {
    width: 18px;
    height: 18px;
    top: -24px;
    right: -3px;
    background-size: 8px 8px;
  }
}
